import React, { Component } from 'react';
import './DisplayTable.scss';
import PropTypes from "prop-types";

class DisplayTable extends Component {
    constructor(props){
        super(props);   

        this.state = {
           
        }
    }

    displayUsers = tableUsers => {
        
       return tableUsers.map((row, index) => {
          return (
            <tr  key={index}>
                {/* <td><img src="https://i.picsum.photos/id/1005/100/100.jpg" alt="" /></td> */}
                <td>{row.firstName}</td>
                <td>{row.surname}</td>
                <td>{row.email}</td>
                <td>{row.cellphone}</td>
                <td>{row.comments}</td>
            </tr>

          );
        });
      };
    

    render() {
        let {tableTitle, userList} = this.props;
        return (
            <div className={'table-container'}>
                
                <div class="table-users">
                    <div class="header">{tableTitle}</div>
                    <table cellspacing="0">
                        <tr>
                            <th>Name</th>
                            <th>Surname</th>
                            <th>Email</th>
                            <th>Phone</th>
                            <th width="230">Comments</th>
                        </tr>
                            {this.displayUsers(userList)}
                    </table>
                </div>
            </div>
        )
    }
}

DisplayTable.propTypes = {
    tableTitle: PropTypes.string,
    userList: PropTypes.array
};

export default DisplayTable;