import * as actionTypes from '../actions/actionTypes';
import { getUrl, login, logout  } from '../middleware/connectAPI';

let API_URL = getUrl();
var _store = null;

const onlogout = res => {
    _store.dispatch({ type: actionTypes.SEND_LOGOUT, value: res});
};

const onlogin = res => {
    _store.dispatch({ type: actionTypes.SEND_LOGIN, value: res });
};

const signalRMiddleware = (store) => next => async (action) => {
    _store = store;
    await handleActionType(action.type, action.value);
    return await next(action);
};

const handleActionType = async (type, value) => {
    switch (type) {
        default:
            break;
    }
}



export default signalRMiddleware;