import React, { Component } from 'react';
import './Ministries.scss';
import discipleIcon from '../../images/disciple.png';
// import studyIcon from '../../images/filters.png';
// import statsIcon from '../../images/statistics.png';
// import profileIcon from '../../images/user.png';

import yq from '../../images/yq.jpg';
import ck from '../../images/ck.png';
import aow from '../../images/aow.jpg';
import fk from '../../images/fk.png';
import ob from '../../images/olea.jpg';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getAppReduxStateObject } from '../../reduxStateMapping';



class Ministries extends Component {
    constructor(props){
        super(props);
    
        this.state = ({
         imgUrl:yq
        })
    }
    
    switchUrl(url){
      let {imgUrl} = this.state;
      this.setState({
        imgUrl : url
      })
    }

    render() {
       
        let nav_name1="CK", nav_name2="AOW", nav_name3="YQ", nav_name4="FK", nav_name5="Olea-Bond"; 
        let {imgUrl} = this.state;
        
        return (
            <div className={'ministies'}>
       
                <img className={'img-ministries'} src={imgUrl} />
                {
                <div className={'bottom-nav'}>
                    <div className={'sticky-footer'}>

                    <nav class="nav horizontal">
                        <ul>
                            <li className={'one-fourth'}>
                                <div onClick={()=>this.switchUrl(ck)}> <a ><img class="sticky-search" src={discipleIcon} />
                                        <p>{nav_name1}</p>
                                    </a>
                                </div>
                            </li>
                            <li className={'one-fourth'}>
                                <div onClick={()=>this.switchUrl(aow)} >  <a ><img class="sticky-filters" src={discipleIcon} />
                                    <p>{nav_name2}</p> </a>
                                </div>
                            </li>
                            <li className={'one-fourth'}>
                                <div onClick={()=>this.switchUrl(yq)} > <a ><img class="sticky-map" src={discipleIcon} />
                                    <p>{nav_name3}</p> </a>
                                </div>
                               
                            </li>
                            <li className={'one-fourth'}>
                                <div onClick={()=>this.switchUrl(fk)}><a><img class="sticky-radius" src={discipleIcon}/>
                                    <p>{nav_name4}</p> 
                                    </a>
                                </div>
                            </li>
                            <li  className={'one-fourth'}>
                                <div onClick={()=>this.switchUrl(ob)} > 
                                     <a><img class="sticky-radius" src={discipleIcon}/>
                                        <p>{nav_name5}</p>
                                    </a>
                                </div>
                            </li>
                        </ul>
                     </nav>
                    </div>
                </div>
                }
            </div>
        )
    }
}


export default connect(null, { ...getAppReduxStateObject() })(withRouter(Ministries));
