import React, { Component } from 'react'
import './MyDisciple.scss';
import TableRow from './TableRow';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getAppReduxStateObject } from '../../reduxStateMapping';
import discipleIcon from '../../images/disciple.png';
import studyIcon from '../../images/filters.png';
import statsIcon from '../../images/statistics.png';
import profileIcon from '../../images/user.png';
import TableDisplay from '../util/DisplayTable';
import NewDisciple from './NewDisciple';
import Profile from '../Profile/Profile'


class MyDisciple extends Component {
    constructor(props){
        super(props);   
        this.state = {
            pageUrl: 'viewall'
        }
    }

    switchPage = (page, userList) =>{
        let {tableTitle='List of Disciplers', username, fName, surname, province, dissciples, email, region, town, branch, title } = this.props;
        switch(page){
            case 'viewall':
                return <TableDisplay tableTitle={tableTitle} userList={userList}/>
            case 'newuser':
                return <NewDisciple
                username={username} fName={fName} surname={surname} province={province} dissciples={dissciples} email={email} region={region} town={town} branch={branch} title={title}
            />
            case 'profile':
                return <Profile/>
            default:
                return <TableDisplay tableTitle={tableTitle} userList={userList}/>
        }
      }

    render() {
        let userList = [
            {
                firstName: 'My Name',
                surname:'Surname',
                email:'mabasolindaniricardo@gmail.com',
                cellphone:'0711171524',
                comments:'Assurance class is currently progressing well'
            },
            {
                firstName: 'My Name',
                surname:'Surname',
                email:'mabasolindaniricardo@gmail.com',
                cellphone:'0711171524',
                comments:'Assurance class is currently progressing well'
            },
            {
                firstName: 'My Name',
                surname:'Surname',
                email:'mabasolindaniricardo@gmail.com',
                cellphone:'0711171524',
                comments:'Assurance class is currently progressing well'
            },
            {
                firstName: 'My Name',
                surname:'Surname',
                email:'mabasolindaniricardo@gmail.com',
                cellphone:'0711171524',
                comments:'Assurance class is currently progressing well'
            },
            {
                firstName: 'My Name',
                surname:'Surname',
                email:'mabasolindaniricardo@gmail.com',
                cellphone:'0711171524',
                comments:'Assurance class is currently progressing well'
            },{
                firstName: 'My Name',
                surname:'Surname',
                email:'mabasolindaniricardo@gmail.com',
                cellphone:'0711171524',
                comments:'Assurance class is currently progressing well'
            }
        ];
        let nav_name1="All Disciplers", nav_name2="New Discipler", nav_name3="Progress", nav_name4="Profile";  
        let {pageUrl} = this.state;

        return (
            <div className={'discipler-container'}>
                 {this.switchPage(pageUrl,userList)}           
                <div className={'bottom-nav'}>
                    <div className={'sticky-footer'}>
                    <nav class="nav horizontal">
                        <ul>
                            <li className={'one-fourth'}>
                                <a onClick={()=>this.setState({pageUrl:'viewall'})}><img class="sticky-search" src={studyIcon} />
                                    <p>{nav_name1}</p>
                                </a>
                            </li>
                            <li className={'one-fourth'}>
                                <a  onClick={()=>this.setState({pageUrl:'newuser'})}><img class="sticky-filters" src={discipleIcon} />
                                    <p>{nav_name2}</p>
                                </a>
                            </li>
                            <li className={'one-fourth'}>
                                <a ><img class="sticky-map" src={statsIcon} />
                                    <p>{nav_name3}</p> 
                                </a>
                            </li>
                            <li className={'one-fourth'}>
                                <a  onClick={()=>this.setState({pageUrl:'profile'})}><img class="sticky-radius" src={profileIcon}/>
                                    <p>{nav_name4}</p> 
                                </a>
                            </li>
                        </ul>
                     </nav>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = ({ userReducer={} }) => (
    {
        isLoggedIn: userReducer.isLoggedIn,
        isLoadingData: userReducer.isLoadingData,
        designation: userReducer.designation,
        username: userReducer.username,
        fName: userReducer.fName,
        surname: userReducer.surname,
        province: userReducer.province,
        dissciples: userReducer.dissciples,
        email: userReducer.email,
        region: userReducer.region,
        town:userReducer.town,
        branch:userReducer.branch,
        title:userReducer.title    
    }
);

export default connect(mapStateToProps, { ...getAppReduxStateObject() })(withRouter(MyDisciple));
