import React, { Component } from 'react';
import './Profile.scss';
import PropTypes from "prop-types";
import $ from 'jquery';

class Profile extends Component {
    componentDidMount(){
        $('.name-label').click(() => {
            $('.details-wrapper').toggleClass('open');
            $('.background-image-wrapper').toggleClass('open');
        });
    }

    activateJs = () =>{
        $('.name-label').click(() => {
            $('.details-wrapper').toggleClass('open');
            $('.background-image-wrapper').toggleClass('open');
        });
    }

    render() {          
        let { username='Username',fName='Name',surname='Surname',province,dissciples,cellphone='+27677200766',email='rebecca.watson@about.me',designation,branch,title='Mr'} = this.props;
        this.activateJs();
        return (
            <div className="profile-container">
              <div class="phone-wrapper">
                    <div className="background-image-wrapper">
                    </div>

                    <div className="details-wrapper">
                        <div className="names-wrapper">
                            <div className="name-label">
                               {title} {fName} {surname} 
                            </div>
                            <div className="person-details">
                                <div class="description">
                                    This is the description about me, I am either loggedIn as an Admin, Bishop or a discipler
                                </div>
                                <div className="phone">
                                    {cellphone}
                                </div>
                                <div className="email">
                                    {email}
                                </div>
                                <div className="province">
                                    {province}
                                </div>
                                <div className="branch">
                                    {branch}
                                </div>
                                <div className="designation">
                                    {designation}
                                </div>
                                <div className="username">
                                    {username}
                                </div>
                            </div>
                        </div>
                        
                        <div className="stats-wrapper">
                            <div className="stats">
                                <div className="stats-count">
                                    {dissciples?.length ?? 12}
                                </div>
                                <div className="label">
                                   Dissciples
                                </div>
                            </div>
                            
                            <div className="stats">
                                <div className="stats-count">
                                   7
                                </div>
                                <div className="label">
                                 Finished Class
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


                <div class="footer-note">
                <div class='ajduke'>
                </div>

                </div>
            </div>
        )
    }
}

Profile.propTypes = {
    designation: PropTypes.string,
    username: PropTypes.string,
    fName: PropTypes.string,
    surname: PropTypes.string,
    province: PropTypes.string,
    dissciples: PropTypes.string,
    email: PropTypes.string,
    region: PropTypes.string,
    town: PropTypes.string,
    branch: PropTypes.string,
    title: PropTypes.string,
    cellphone:PropTypes.string
};


export default Profile;