import * as actionTypes from '../actions/actionTypes';

export default (state = {
    isLoggedIn:false,
    isLoadingData:true,
    discipleId:null,
    title:null,
    fName:null,
    surname:null,
    cellNumber:null,
    province:null,
    region:null,
    town:null,
    branch:null,
    suburb:null,
    email:null,
    designation:null,
    username:null,
    comment:null,
    dissciples:{}

}, action) => {
    switch(action.type){
        case actionTypes.GET_USER_SUCCESS:
            var updatedState = {};
            state = {};
            var updatedState =  JSON.stringify(action.response);
            var updatedState =  JSON.parse(updatedState);

            var newState = state;
            var updatedState = action?.data;
            state.isLoggedIn  = true;
            state.isLoadingData = false;
            newState.dissciples = updatedState;
            return {
                ...state, ...newState
            }
        case actionTypes.SEND_LOGIN:
            console.log('Update User Login Reducer: ',action.response ) 
            var updatedState = {}
            var updatedState =  JSON.stringify(action.response);
            var updatedState =  JSON.parse(updatedState);

            state.isLoggedIn = true;
            state.isLoadingData = false;
            state.discipleId =  updatedState.discipleId;
            state.title =  updatedState.title;
            state.fName =  updatedState.fName;
            state.surname = updatedState.surname;
            state.cellNumber = updatedState.cellNumber;
            state.province = updatedState.province;
            state.region =  updatedState.region;
            state.town =  updatedState.town;
            state.branch = updatedState.branch;
            state.suburb = updatedState.suburb;
            state.email = updatedState.email;
            state.designation = updatedState.designation;
            state.username = updatedState.username;
            state.comment = updatedState.comment;
            return {
               ...state, ...updatedState
            }
        case actionTypes.SEND_LOGOUT:
            console.log('Logout')
            var updatedState = {}
            var updatedState =  JSON.stringify(action.response);
            var updatedState =  JSON.parse(updatedState);

            state.isLoggedIn  = false;
            return {
                ...state, ...updatedState
            }
        default:
            return state;
    }
};