import React, { Component } from 'react'
import './Header.scss';
import LogoutIcon from '../../images/logout.svg';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getAppReduxStateObject } from '../../reduxStateMapping';
 class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
                path: '/'
            }
      }

      doLogout = () =>{
        let {isLoggedIn} = this.props;
        this.props.logout();
        if(isLoggedIn){
            this.setState({path:'/login'})
        }
      }
    
    // componentDidMount(){
    //     this.doLogout();
    // }

    // componentDidUpdate(){
    //     this.doLogout();
    // }
    
    render() {
        let {isLoggedIn} = this.props;
        let {path} = this.state;
        return (
            <div className={'header-container'}>
                <div className={'status'}>
                    
                </div>
                <div className={'headertitle'}>
                    BBM App
                </div>
                <a href={path} onClick={this.doLogout} className={'logout'}><img src={LogoutIcon}/></a>
            </div>
        )
    }
}

const mapStateToProps = ({ userReducer={} }) => (
    {
        isLoggedIn: userReducer.isLoggedIn
    }
  );

export default connect(mapStateToProps, { ...getAppReduxStateObject() })(withRouter(Header));
