import React, { Component } from 'react';
import './NewDisciple.scss';
import PropTypes from "prop-types";

class NewDisciple extends Component {
    constructor(props) {
        super(props)
        this.state = {
            designation:null,
            username:null,
            fName:null,
            surname: null,
            province: null,
            dissciples:null,
            email:null,
            region:null,
            town:null,
            branch:null,
            title:null
        }
      }

    handleSubmit = (e) =>{
    
    }

    render() {
        let { username,fName,surname,province,dissciples,email,region,town,branch,title} = this.props;

        return (
            <div className={'new-disciple-container'}>
            <div className={'form'}>
                <p/>
                <p class="light">New Disciple</p>
                <div className="inputs">
                    <input type="text" name="name" onChange={this.getUsername} placeholder="Name"/>
                    <input type="text" name="surname"  onChange={this.getPassword}  placeholder="Surname"/>
                    <input type="text" name="cellphone" onChange={this.getUsername} placeholder="Cellphone"/>
                    <input type="text" name="email"  onChange={this.getPassword}  placeholder="Email"/>
                    <input type="text" name="province" onChange={this.getUsername} placeholder="Province"/>
                    <input type="text" name="town"  onChange={this.getPassword}  placeholder="Town"/>
                    <input type="text" name="branch" onChange={this.getUsername} placeholder="Branch"/>
                </div >
            </div>
            <footer>
                <button onClick={()=>this.handleSubmit()}>Save</button>
            </footer>
        </div>
        )
    }
}

NewDisciple.propTypes = {
    designation: PropTypes.string,
    username: PropTypes.string,
    fName: PropTypes.string,
    surname: PropTypes.string,
    province: PropTypes.string,
    dissciples: PropTypes.object,
    email: PropTypes.string,
    region: PropTypes.string,
    town: PropTypes.string,
    branch: PropTypes.string,
    title: PropTypes.string
};

export default NewDisciple;