import React, { Component } from 'react';
import { Switch, Route, Redirect } from "react-router-dom";
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getAppReduxStateObject } from '../../reduxStateMapping';
import history from '../../helpers/history';
import './Login.scss';
import BBMLogo from '../../images/bmm.jpeg';
import * as pageUrls from '../constants/pageUrls';
import { enforcePath, enforceActiveElements } from '../../helpers/generalHelper';
import BishopComponent from "../Bishop/Bishop";
import AdminComponent from "../Admin/Admin";
import DisciplerComponent from "../Discipler/MyDisciple"; 
import Header from "../Header/Header";

class LoginForm extends Component {
    constructor(props) {
        super(props)
        this.state = {
          username:'',
          password:'',
          role:0
        }
      }

    // componentDidMount() {
    //     this.enforceAll();
    // }

    
    // componentDidUpdate = () => {
    //     this.enforceAll();
    // }

    enforceAll = () => {
        let { isLoggedIn, history,designation } = this.props;
        this.setState({role:designation?.toUpperCase()})
        let {role} = this.state;
        enforcePath(isLoggedIn, history, role);
      //  enforceActiveElements();
    }

    handleSubmit = (e) =>{
        let {username,password} = this.state;
        this.props.login(username,password);
        this.checkIsValidUser();
        this.enforceAll();
        this.forceUpdate();
    }

    getUsername = (event) => {
        this.setState({username:event?.target?.value ?? null});
    }

    getPassword = (event) =>{
        this.setState({password:event?.target?.value ?? null});
    }

    doRefresh = () => {
      return (
        <Switch>
          {this.enforceAll()}
          <Route exact path={pageUrls.ADMIN}>
            <Header />
            <AdminComponent />
          </Route>
          <Route exact path={pageUrls.DISCIPLER}>
            <Header />
            <DisciplerComponent />
          </Route>
          <Route exact path={pageUrls.BISHOP}>
            <Header />
            <BishopComponent />
          </Route>
          <Redirect to={"/"} />
      </Switch>
      )
    }


    checkIsValidUser = () =>{
      let { isLoggedIn, designation } = this.props;
      this.setState({role:designation?.toUpperCase()})
      let {role} = this.state;
      let { pathname='' } = history?.location ?? {};

      if(isLoggedIn && this.state.username === this.props.username){
          
          switch(role){ 
            case "DISCIPLER":
              history.push(pageUrls.DISCIPLER);
              return    <DisciplerComponent />;
              break;
            case "BISHOP":
              history.push(pageUrls.BISHOP);
              return   <BishopComponent />;
              break;
            case "ADMIN":
              history.push(pageUrls.ADMIN);
              return   <AdminComponent />;
              break;
            default:
              history.push(pageUrls.LOGIN);
              break;
          }
         this.forceUpdate();
        }
    }

    displayLogin = () =>{
      return <div className={'login-container app'}>
                
      <div className="bg"></div>
      <div className={'form'}>
          <header>
              <img src={BBMLogo}/>
          </header>
          <div className="inputs">
              <input type="text" name="" onChange={this.getUsername} placeholder="username"/>
              <input type="password" name=""  onChange={this.getPassword}  placeholder="password"/>

              <p class="light"><a href="#">Forgot password?</a></p>
          </div>
      </div >
      <footer>
          <button onClick={()=>this.handleSubmit()}>Continue</button>
          <p>Don't have an account? <a href="/">Go back</a></p>
      </footer>
    </div>
    }
    
    render() {
        //this.enforceAll();
        return this.displayLogin();
    }
  }
  
const mapStateToProps = ({ userReducer={} }) => (
  {
      isLoggedIn: userReducer.isLoggedIn,
      isLoadingData: userReducer.isLoadingData,
      designation: userReducer.designation,
      username: userReducer.username,
      fName: userReducer.fName,
      surname: userReducer.surname,
      province: userReducer.province    
  }
);

export default connect(mapStateToProps, { ...getAppReduxStateObject() })(withRouter(LoginForm));
