import * as actionTypes from './actionTypes';
import axios from 'axios';

export const getUrl = () => {
    return  'https://localhost:44310/api/Users';
};

const url =  getUrl();
let axiosConfig = {
    withCredentials: true,
};

export const getAxiosInstance = () => {
    var url = getUrl();
    var headers = { headerType: 'example header'}
    return axios.create({ baseURL: url , headers});
};

const getUserSuccess = (data) => {
    return {
        type: actionTypes.GET_USER_SUCCESS,
        data: data
    }
}
 
export const getAllUsers = (props) => { 
    return (dispatch) => {
        axios.get(url)
        .then(response => {
            dispatch(getUserSuccess(response.data[0]));
        })
        .catch(error => {
            console.log('failed to get user', error);
        })
    }
}

const postDataSuccess = (response) => {
    return {
        type: actionTypes.POST_USER_SUCCESS,
        response: response
    }
}
 
export const insertUser = (url, obj, props) => {
    return (dispatch) => {
        axios.post(url, obj)
        .then(response => {
            dispatch(postDataSuccess(response));
        })
        .catch(error => {
            console.log('failed to insert user', error);
        })
    }
}
 
const putDataSuccess = (response) => {
    return {
        type: actionTypes.PUT_USER_SUCCESS,
        response: response
    }
}
 
export const updateUser = (url, obj, props) => {
    return (dispatch) => {
        axios.put(url, obj)
        .then(response => {
            dispatch(putDataSuccess(response));
        })
        .catch(error => {
            console.log('failed to update user', error);
        })
    }
}
 
const deleteDataSuccess = (response) => {
    return {
        type: actionTypes.DELETE_USER_SUCCESS,
        response: response
    }
}
 
export const deleteUser = (url, props) => {
    return (dispatch) => {
        axios.delete(url)
        .then(response => {
            dispatch(deleteDataSuccess(response));
        })
        .catch(error => {
            console.log('failed to delete user', error);
        })
    }
}

const updateLoginUserSuccess = (response) => {
    return {
        type: actionTypes.SEND_LOGIN,
        response: response
    }
}

export const login = (username,password) =>{
    try
    {   
        const userDetails = {
            'Username': username,
            'Password': password
        };
        
        return (dispatch) => {
            axios.post('https://localhost:44310/api/Users/Login', userDetails,axiosConfig)
            .then(response => {
                dispatch(updateLoginUserSuccess(response.data)); 
            })
            .catch(error => {
                console.log('failed to delete user', error);
            })
            
        }          
    }catch(err){
        return false;
    }
}


const updateLogoutSuccess = (response) => {
    return {
        type: actionTypes.SEND_LOGOUT,
        response: response
    }
}

export const logout =  () =>{
    try
    { //await clearToken();    
        return (dispatch) => {
            dispatch(updateLogoutSuccess({}));
        }
    }      
    catch(err){
        return false;
    }
}
