import { isNullOrUndefined } from 'util';
import * as pageUrls from '../components/constants/pageUrls';
import {history} from './history';


export const enforceActiveElements = () => {
    let inputBox = document.getElementsByName('input');
    if(!isNullOrUndefined(inputBox)){
        inputBox.clear();
        inputBox.focus();
    }
    
};

export const enforcePath = (isLoggedIn, history, role) => {
    let { pathname='' } = history?.location ?? {};
    if(isLoggedIn){
        switch(role){
        case "DISCIPLER":
            if(pathname !== pageUrls.DISCIPLER){
                history.push(pageUrls.DISCIPLER);
            }
            break;
        case "BISHOP":
            if(pathname !== pageUrls.BISHOP){
                history.push(pageUrls.BISHOP);
            }
            break;
        case "ADMIN":
            if(pathname !== pageUrls.ADMIN){
                history.push(pageUrls.ADMIN);
            }
            break;
        default:
            if(pathname!==pageUrls.LOGIN){
                history.push(pageUrls.LOGIN);
            }
            break;
        }
    }
    else{
        if(pathname !== pageUrls.LOGIN){
            history.push(pageUrls.LOGIN)
        }
    }
};
