import React, { Component } from 'react';
import './About.scss';
import DisplayMsg from '../LandingPage/DisplaySlides';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getAppReduxStateObject } from '../../reduxStateMapping';

class About extends Component {
 
    render() {
        return (
            <div className={'about'}>
               About 
            </div>
        )
    }
}

export default connect(null, { ...getAppReduxStateObject() })(withRouter(About));
